<template>
  <div class="app">
    <div class="column">
      <h1>{{ msg }}</h1>
      <textarea id="all-words-input" v-model="allWordsMessage" placeholder="input words, separate by new line"></textarea>
      <br>
      <button @click="handleSubmitWords">submit words</button> &nbsp;&nbsp;
      <button @click="handleSubmitText">submit text</button>
    </div>
    <div class="column">
      <h1>{{msg1}}</h1>
      <div>
        <ul>
          <div v-for="uw in unknownWords" :key="uw">
            <li style="align-content: left;">
              <input type="checkbox" :id="uw" :value="uw" v-model="selectedWords" @change="handleKnownWord(uw)">            <label :for="uw">{{uw}}</label>
            </li>
            <br>
          </div>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Vocabulary',
  props: {
  },
  data() {
    return {
      msg: "Filter",
      msg1: "Pick",
      allWordsMessage: "",
      unknownWords: [],
      selectedWords: []
    }
  },
  methods: {
    handleSubmitWords() {
      var words = this.allWordsMessage.split("\n");

      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },

        body: JSON.stringify({
          words: words,
        })
      };
      fetch("https://alihk.clpsz.com/vocabulary/filter-unknown", requestOptions)
        .then(async response => {
          const respData = await response.json();
          this.unknownWords = respData.data;
          console.log("handleSubmitWords succeed, unknown words count " + respData.data.length);
          console.log(respData.data.join(' '))
        });
    },
    handleSubmitText() {
      var text = this.allWordsMessage;

      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },

        body: JSON.stringify({
          text: text,
        })
      };
      fetch("https://alihk.clpsz.com/vocabulary/filter-unknown-from-text", requestOptions)
        .then(async response => {
          const respData = await response.json();
          this.unknownWords = respData.data;
          console.log("handleSubmitText succeed, unknown words count " + respData.data.length);
          console.log(respData.data.join(' '))
        });
    },
    handleKnownWord(word) {
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },

        body: JSON.stringify({
          word: word,
          status: 1,
        })
      };

      fetch("https://alihk.clpsz.com/vocabulary/word-status", requestOptions)
        .then(response => {
            response.json();
            console.log(`handleKnownWord ${word} suceed`)
          });
    }
  },
}
</script>

